@import '~normalize.css/normalize.css';
@import '~typeface-fira-sans/index.css';


/* Global */

html,
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html,
body,
#root,
#pokedex-app {
  height: 100%;

  font-family: 'Fira Sans', sans-serif;
  color: #212121;
}
#pokedex-app a {
  color: #5d4037;
  text-decoration: none;
}

.emoji {
  display: inline-block;
}


/* App structure */

.pk-structure {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.pk-structure--header {
  height: 3.5rem;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-positive: 0;
      flex-grow: 0;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-pack: center;
      justify-content: center;

  background-color: #b71c1c;
}

.pk-structure--body {
  -ms-flex-negative: 1;
      flex-shrink: 1;
  -ms-flex-positive: 1;
      flex-grow: 1;
  overflow: auto;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;

  background-color: #f5f5f5;
}

.pk-header,
.pk-main,
.pk-footer {
  width: 100%;
  max-width: 800px;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-preferred-size: auto;
      flex-basis: auto;
}

.pk-main {
  -ms-flex-positive: 1;
      flex-grow: 1;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}


/* Blocks, elements and modifiers (BEM) */

@import './styles/header.css';
@import './styles/search-input.css';
@import './styles/pokemon-list.css';
@import './styles/pokemon-slot.css';
@import './styles/footer.css';



